import * as THREE from "three"
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js"
import { DRACOLoader } from "three/examples/jsm/loaders/DRACOLoader.js"
import { gsap} from "gsap/all"

/**
 * Load Models more gooder
 * ////////////////////////////////////////////////////////////////////////////////////////////////////////////
 * ////////////////////////////////////////////////////////////////////////////////////////////////////////////
 */

const dracoLoader = new DRACOLoader()
dracoLoader.setDecoderPath('./draco/')
const gltfLoader = new GLTFLoader()
gltfLoader.setDRACOLoader(dracoLoader)

let modelsArray = []
const modelGroup = new THREE.Group()
let mixerArray = []

function loadModel(name, scale, number) {
  // Figure this out, how to group grid with model
  const group = new THREE.Group();

  gltfLoader.load("./models/" + name, (gltf) => {

    if(gltf.animations.length > 0){
      const mixer = new THREE.AnimationMixer(gltf.scene)
      const action = mixer.clipAction(gltf.animations[0])

      mixerArray.push(mixer)

      action.play()
    }

    gltf.scene.scale.set(scale, scale, scale);

    // gltf.scene.position.set(scale, -(scale / 2), 0);
    gltf.scene.position.set(scale, 0, 0);
    gltf.scene.traverse(function (node) {
      if (node.isMesh) {
        node.castShadow = true;
      }
    });
    gltf.scene.traverse(function (node) {
      if (node.isMesh) {
        node.castShadow = true;
      }
    });
    let model = gltf.scene;

    //create name so that we can find and delete later
    model.name = number;

    let bbox = new THREE.Box3().setFromObject(model);
    let helper = new THREE.Box3Helper(bbox, new THREE.Color(0, 255, 0));
    let modelSize = bbox.getSize(new THREE.Vector3());
    const myModel = {
      model: model,
      size: modelSize,
    };

    modelsArray[number] = myModel;
    modelGroup.add(myModel.model);

    console.log(modelGroup)
  });
}

function setRatioMode(yes) {
  if(yes){
    for(let i = 0 ; i < modelGroup.children.length; i++){
      // modelGroup.children[i].position.set(modelGroup.children[i].position.x, -(modelGroup.children[i].position.x / 2), 0)
      gsap.to(modelGroup.children[i].position, {
        x: modelGroup.children[i].position.x,
        y: - 15 - (modelGroup.children[i].position.x / 2),
        z: 0,
        duration: 1.5,
      })
    }
  } else {
    for(let i = 0 ; i < modelGroup.children.length; i++){
      gsap.to(modelGroup.children[i].position, {
        x: modelGroup.children[i].position.x,
        y: 0,
        z: 0,
        duration: 1.5,
      })
    }
  }
}

// modelGroup.position.x = modelGroup.position.x - 0.3
// modelGroup.position.z = modelGroup.position.z - 0.3
// modelGroup.position.y = modelGroup.position.y + 1

export {loadModel, modelsArray, modelGroup, setRatioMode, mixerArray}