import * as THREE from "three";

/**
 * Create the plane that cuts through the 1u entity for measurement and numeric display
 */
const planeGroup = new THREE.Group()

const linePlaneGeometry = new THREE.PlaneGeometry(8, 2.05)

// const linePlaneGeometry = new THREE.BoxGeometry(7, 2.025, 0.2, 4, 4, 4)

const material = new THREE.MeshPhysicalMaterial({
  color: 0xeeeeee,
  emissive: 0xaaaaaa,
  roughness: 0,
  metalness: 0,
  clearcoat: 0,
  clearcoatRoughness: 0,
  transmission: 1,
  ior: 1,
  envMapIntensity: 0
});

const plane = new THREE.Mesh(linePlaneGeometry, material);
plane.position.x = 2.7;
plane.position.z = 1.7;
plane.position.y = 1;
plane.rotateY(-(Math.PI / 4))

planeGroup.add(plane)

/**
 * Create lines on measurement panel
 */
createMoreLines(-0.01)
function createMoreLines(height) {

  const material = new THREE.MeshBasicMaterial({
    color: 0x124951,
  });

  const linePlaneGeometry = new THREE.PlaneGeometry(0.02, 0.2);

  for(let i = 0; i <= 2; i = i + 0.1){

    i = Math.round(i * 10) / 10

    const plane = new THREE.Mesh(linePlaneGeometry, material);
    if(i % 1 == 0){
      plane.scale.set(2, 2, 1)
      plane.position.x = -0.085;
      plane.position.z = -1.08;
    } else {
      plane.position.x = -0.15;
      plane.position.z = -1.1;
    }
    plane.position.y = i;
    plane.rotateY(-(Math.PI / 4))
    plane.rotateZ(-(Math.PI / 2))

    planeGroup.add(plane);
  }
}

export {planeGroup}