/**
 * Animate
 */
const clock = new THREE.Clock();
let previousTime = 0;

/**
 * Parallax
 */
const cursor = {}
cursor.x = 0
cursor.y = 0

window.addEventListener('mousemove', (event) => {
    cursor.x = event.clientX / sizes.width - 0.5
    cursor.y = event.clientY / sizes.height - 0.5
})

let matrix = new THREE.Matrix4()

const tick = () => {
  const elapsedTime = clock.getElapsedTime();
  const deltaTime = elapsedTime - previousTime;
  previousTime = elapsedTime;

  // Mixer
  if(mixerArray.length > 0) {
    mixerArray.forEach((mixer) => {
      // if(mixer._root.name == 3){
        mixer.update(deltaTime)
      // }
    })
  }

  const parallaxX = cursor.x
  const parallaxY = - cursor.y

  // Parallax
  if(!secondView && !currentlyScaling){
    // cameraGroup.rotation.y = parallaxX * 0.3

    modelGroup.children.forEach((model) => {
      if(model.name == "3"){
        // model.rotation.y = parallaxX * 2
        // model.rotation.x = -(parallaxY * 0.5)

        gsap.to(model.rotation, {
          x: -(parallaxY * 0.5),
          y: parallaxX * 2,
          z: 0,
        });

      }
      // model.rotation.x = parallaxY * 0.1
      // model.rotation.z = parallaxY * 0.1
    })

    // cameraGroup.position.y = parallaxY * 0.5

    matrix.makeRotationY(Math.PI / 5000)
  } else if(currentlyScaling || secondView) {

    modelGroup.children.forEach((model) => {
      gsap.to(model.rotation, {
        x: 0,
        y: 0,
        z: 0,
      });
    })

  }

  // ratioGroup.children.forEach((number) => {
  //   number.rotation.y += 0.02
  // })

  // Render
  renderer.render(scene, camera);

  // Call tick again on the next frame
  window.requestAnimationFrame(tick);
};

tick();

