/**
 * When a scaling action happens we have to delete one model and load another
 * @param {*} up boolean saying whether it is an up scaling action or not
 */
function resetModels(up) {
  if (up) {
    // modelGroup.children[4].removeFromParent()
    modelGroup.children.forEach((child) => {
      if (child.name === 1) {
        child.removeFromParent();
      }
    });
    modelGroup.children.forEach((child) => {
      child.name--;
    });
    loadNewModel(true);
  } else {
    // modelGroup.children[4].removeFromParent()
    modelGroup.children.forEach((child) => {
      if (child.name === 5) {
        child.removeFromParent();
      }
    });
    modelGroup.children.forEach((child) => {
      child.name++;
    });
    loadNewModel(false);
  }
}

// loadNewModel()

function incrementCurrentWorld(add) {
  if(add){
    currentWorld++
  } else {
    currentWorld--
  }
}

const tl = gsap.timeline()

function goToSecondView(yes) {
  if(yes) {
    cameraGroup.add(measurementPlane.planeGroup)

    removeHorizonPlane()

    tl.to(camera.position, {
      x: 0,
      y: 1.5,
      z: 3.5,
      onUpdate: function () {
          document.getElementById("button-shift-camera").textContent = '↩';
          document.getElementById("entity-labels-side").style.display = 'none';
          // document.getElementById('second-overlay').style.display = 'none';
          document.getElementById("label-mode").style.display = 'none';
      },
      onComplete: function () {
          document.getElementById('interface').style.display = 'block';
          moveToSecondView(false)
          // controls.enabled = false;
      },
      });
      tl.to(camera.rotation, {
      x: 0,
      y: -(Math.PI / 4),
      z: 0,
    })
  } else {
    cameraGroup.remove(measurementPlane.planeGroup)

    addHorizonPlane()

    scene.add()

    tl.to(camera.position, {
      x: -4,
      y: 3,
      z: 0,
      onUpdate: function () {
          document.getElementById('interface').style.display = 'none';
          // document.getElementById('second-overlay').style.display = 'block';
          document.getElementById("button-shift-camera").textContent = '↪';
      },
      onComplete: function () {
          moveToSecondView(true)
          document.getElementById("entity-labels-side").style.display = 'block';
          document.getElementById("label-mode").style.display = 'none';
          // controls.enabled = true;
      },
      });
      tl.to(camera.rotation, {
      x: 0,
      y: - (Math.PI / 2),
      z: 0,
    })
  }
}

function goToRatioView(yes) {
  if(yes) {
    cameraGroup.add(measurementPlane.planeGroup)

    tl.to(camera.position, {
      x: 0,
      y: 1.5,
      z: 3.5,
      duration: 2,
      onUpdate: function () {

          camera.fov = 75;
          camera.updateProjectionMatrix();

          // make overview panel icons white
          // let icons = document.querySelectorAll('.overview-icon')
          // for(let i = 0; i < icons.length; i++){
          //   icons[i].classList.remove('dark-theme')
          // }
          // renderer.setClearColor(0xffffff, 0.7);
          document.getElementById("button-ratio-mode").textContent = '1:10';
          document.getElementById('ratio-overlay').style.display = 'none';
          scene.remove(ratioGroup)
      },
      onComplete: function () {
          document.getElementById('interface').style.display = 'block';
          controls.enabled = false;
          scene.fog = new THREE.Fog(0xffffff, 60, 200);
      },
      });
      tl.to(camera.rotation, {
        x: 0,
        y: -(Math.PI / 4),
        z: 0,
      })
  } else {
    cameraGroup.remove(measurementPlane.planeGroup)
    console.log("going")

    const rot = -0.523599;

    tl.to(camera.position, {
      x: 20,
      y: 2,
      z: 45,
      duration: 1.5,
      onUpdate: function () {
        camera.fov = 100;
        camera.updateProjectionMatrix();

        // make overview panel icons white
        // let icons = document.querySelectorAll('.overview-icon')
        // for(let i = 0; i < icons.length; i++){
        //   icons[i].classList.add('dark-theme')
        // }
        // renderer.setClearColor(0xffffff, 0);
        document.getElementById('interface').style.display = 'none';
        document.getElementById('ratio-overlay').style.display = 'block';
        document.getElementById("button-ratio-mode").textContent = '1:10';
        scene.add(ratioGroup)
      },
      onComplete: function () {
          controls.enabled = true;
          // camera.lookAt(0,0,0)
          scene.fog = new THREE.Fog(0xffffff, 60, 200);
      },
      });
      tl.to(camera.rotation, {
        x: 0,
        y: -(Math.PI / 4) + 0.349066,
        z: 0,
      })
  }
}

