function addHorizonPlane() {

  camera.fov = 100;
  camera.updateProjectionMatrix();

  scene.add(horizonGroup)
  scene.fog = new THREE.Fog(0xffffff, 60, 200);
}

function removeHorizonPlane() {
  if(horizonGroup != null) {

    camera.fov = 75;
    camera.updateProjectionMatrix();

    scene.remove(horizonGroup)
    scene.fog = new THREE.Fog(0xffffff, 60, 100);
  }
}

/**
 * Greate measurement lines on ground
 */
function createGraph() {
  let height = -0.01;
  const linePlaneGeometry = new THREE.PlaneGeometry(0.005, 1000);
  const material = new THREE.MeshBasicMaterial({
    color: 0xe73870,
  });
  const plane = new THREE.Mesh(linePlaneGeometry, material);
  plane.rotateX(-(Math.PI / 2));
  plane.rotateZ(Math.PI / 2);
  plane.position.x = 500;
  plane.position.y = 0.005;
  // scene.add(plane);
  // createMoreLines(height);
}
function createMoreLines(height) {
  const floorGrid = new THREE.Group();

  const material = new THREE.MeshBasicMaterial({
    color: 0x124951,
  });
  const linePlaneGeometry = new THREE.PlaneGeometry(0.02, 0.2);
  // plane.rotateX(-(Math.PI / 4));
  for(let i = 0; i <= 2; i = i + 0.1){
    i = Math.round(i * 10) / 10
    const plane = new THREE.Mesh(linePlaneGeometry, material);
    console.log(i)
    if(i % 1 == 0){
      plane.scale.set(2, 2, 1)
      plane.position.x = -0.085;
      plane.position.z = -1.08;
    } else {
      plane.position.x = -0.15;
      plane.position.z = -1.1;
    }
    plane.position.y = i;
    plane.rotateY(-(Math.PI / 4))
    plane.rotateZ(-(Math.PI / 2))

    floorGrid.add(plane)
  }

  floorGrid.rotateY(Math.PI / 4)
  floorGrid.rotateX(-(Math.PI / 2))

  // scene.add(floorGrid)
}

