import * as THREE from "three"
import { FontLoader } from "three/examples/jsm/loaders/FontLoader.js"
import { TextGeometry  } from 'three/examples/jsm/geometries/TextGeometry.js'

const ratioGroup = new THREE.Group()

// Material
const particlesMaterial = new THREE.PointsMaterial({
  size: 0.05,
  sizeAttenuation: true,
  color: 0x00ffff
})

/**
 * Text
 */
const fontLoader = new FontLoader();

fontLoader.load(
  '/fonts/Recursive Sans Linear Light_Regular.json', (font) => {
    const colonGeo = new TextGeometry(
      ':',
      {
        font: font,
        height: 0.1,
        size: 1
      }
    )

    const textpointone = new TextGeometry(
      '0.1',
      {
        font: font,
        height: 0.1,
        size: 1
      }
    )

    const text1 = new TextGeometry(
      '0.1',
      {
        font: font,
        height: 0.1,
        size: 1
      }
    )

    const text2 = new TextGeometry(
      '1',
      {
        font: font,
        height: 0.1,
        size: 1
      }
    )

    const text3 = new TextGeometry(
      '10',
      {
        font: font,
        height: 0.1,
        size: 1
      }
    )

    const textMaterial = new THREE.MeshBasicMaterial({
      color: 0x124951,
    })

    const colonone = new THREE.Mesh(colonGeo, textMaterial)
    const colon = new THREE.Mesh(colonGeo, textMaterial)
    const colontwo = new THREE.Mesh(colonGeo, textMaterial)
    const colonthree = new THREE.Mesh(colonGeo, textMaterial)
    const colonthreethree = new THREE.Mesh(colonGeo, textMaterial)

    // number texts
    const pointone = new THREE.Mesh(textpointone, textMaterial)
    const one = new THREE.Mesh(text1, textMaterial)
    const ten = new THREE.Mesh(text2, textMaterial)
    const hundred = new THREE.Mesh(text3, textMaterial)

    let height = - 10
    let z = -5

    // Point one
    pointone.position.set(0, height + 1, 0)
    pointone.scale.set(0.1,0.1,0.1)

    // One
    one.position.set(0.65, height + 4, 0)

    // Ten
    ten.position.set(4, height + 6, 0)
    // ten.rotateY(-(Math.PI / 6))
    ten.scale.set(10,10,10)

    // Hundred
    hundred.position.set(50, height + 25, 0)
    hundred.scale.set(100, 100, 100)


    ratioGroup.add(pointone)
    ratioGroup.add(one)
    ratioGroup.add(ten)
    ratioGroup.add(hundred)

  }
)

export {ratioGroup}